import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
import uniNoticeBar from '@/components/uni-notice-bar/uni-notice-bar.vue';

const app = getApp().globalData;
const { appid, appsecret,imgRemote} = app;
export default {
	components: { uniNoticeBar },
	data() {
		return {
			token:uni.getStorageSync('cdj_token'),
			load: true,
			imgRemote:imgRemote,
			speed:30,
			loading:'',
			page:1,
			num:10,
			hours:'',
			minu:0,
			second:0,
			adList: {},
			activeList:{},
			activeConf:{},
			cartware:{},
			config:{},
			itemList:{}
		};
	},
	methods: {
		navUrl(e) {
				let { id, cate_id, status } = e;
				if (status == 0) {
								uni.showToast({
									title:"该栏目已下架",
									duration:2000,
									icon:none
								})
								return;
							}
			switch (id) {
				case 1:
					uni.navigateTo({
						url: '/pages/index/collect'
					});
					break;
				case 2:
					uni.navigateTo({
						url: '/pages/index/newback'
					});
					break;
				case 3:
					uni.navigateTo({
						url: '/pages/index/recommed'
					});
					break;
				case 4:
					uni.makePhoneCall({
						phoneNumber: this.adList.phone
					});
					break;
				case 5:
					uni.switchTab({
						url: '/pages/tabar/classify'
					});
					break;
				case 6:
					uni.switchTab({
						url: '/pages/tabar/shopcart'
					});
					break;
				case 7:
					uni.switchTab({
						url: '/pages/tabar/order'
					});
					break;
				case 8:
					uni.switchTab({
						url: '/pages/tabar/user'
					});
					break;
				default:
					app.globalData.classId = cate_id;
					wx.switchTab({
						url: '/pages/tabar/classify'
					});
					break;
			}
		},
		openCart(item) {
			uni.hideTabBar();
			this.$refs.popup.open();
			this.cartware=item;
		},
		onClose() {
			this.$refs.popup.close();
			uni.showTabBar();
		},
		newPage(url) {
				uni.navigateTo({
					url: `/pages/index/${url}`
				});
		},
		indexAd() {
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign(
				{
					sign: sign
				},
				obj
			);
			rs.getRequests('indexAd', params, res => {
				let data = res.data;
				if (data.code == 200) {
					this.adList = data.data;
				}
			});
		},
		indexItem() {
					let timeStamp = Math.round(new Date().getTime() / 1000);
					let obj = {
						appid: appid,
						timeStamp: timeStamp
					};
					let { num, page } = this;

					let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
					let params = Object.assign(
						{
							page: 1,
							sign: sign,
							page: page,
							num: num
						},
						obj
					);
		
					rs.getRequests('indexItem', params, res => {
						let data = res.data;
						if (data.code == 200) {
							this.itemList=data.data.list;
							this.config=data.data;
							if (data.data.total <= 10) {
								this.loading = false;
							} else {
								this.loading = true;
							}
						}
					});
				},
				//限时抢购
						limitList() {
							let timeStamp = Math.round(new Date().getTime() / 1000);
							let obj = {
								appid: appid,
								timeStamp: timeStamp
							};
							let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
							let params = Object.assign(
								{
									sign: sign
								},
								obj
							);
							rs.getRequests('panicBuyIndex', params, res => {
								let data = res.data;
								if (data.code == 200) {
									let { itemList, timeRemain } = data.data;
									this.hours = Math.abs(timeRemain) / 3600;
									this.activeConf = data.data;
									this.activeList = itemList;
								}
							});
						},
	
	},
	onShow() {
		this.indexAd();
		if(this.page==1){this.indexItem();}
		this.limitList();
	},
	onReachBottom() {
			//页面上拉触底事件的处理函数
			var that = this;
			var timeStamp = Math.round(new Date().getTime() / 1000);
			let { num, page } = that;
			var obj = {
				appid: appid,
				timeStamp: timeStamp
			};
	
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				num: num,
				page: page + 1,
				timeStamp: timeStamp,
				sign: sign
			};
			rs.getRequests('indexItem', data, res => {
				let { data } = res;
				if ((data.code = 200)) {
					if (data.data != '') {
						this.itemList.push(...data.data.list);
						this.page += 1;
						this.loading = true;
					} else {
						this.loading = false;
					}
				}
			});
		},
};
